/* ------------------------------------------- */
/* nav button
/* ------------------------------------------- */
.ui-btn,
.ui-nav{
	position: relative;
	display: inline-block;
    width: 34px;
    height: 34px;
    line-height: 32px;
	padding: 0px;
	margin-bottom: 0;
    text-indent: -9999px;
    overflow: hidden;	
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-ms-touch-action: manipulation;
	touch-action: manipulation;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	background-image: none;
	background-color: transparent;
    background-repeat:no-repeat;
    background-position:center;
    background-size:100%;
	border: 0px solid transparent;
	border-radius: 2px;
	outline: 0;
}
.ui-nav:focus,
.ui-nav:active{
	background-color: rgba(0,0,0,.05);
}
.nav-back{
	background-image: url(data:image/png;base64,);
}
.nav-close{
	background-image: url(data:image/png;base64,);
	position: absolute;
    right: 10px;
    top: 50%;
    width: 24px;
    height: 24px;
    -webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}
.input-btn-group.input-group .form-control:first-child{
	border-right-width: 0;
	border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}
.input-btn-group .input-group-addon:last-child{
	padding: 0;
	border-color: #dbdbdb;
}
.input-btn-group .input-group-addon .btn{
	font-size: 12px;
	line-height: 1;
	padding: 9px 5px;
	background-color: transparent;
}
/* ------------------------------------------- */
/* info-popup
/* ------------------------------------------- */
.icon-link{
    position: relative;
    top: 4px;
}
.ico-alert{
    background-position: -66px -136px !important;
}
.ico-alert-w{
    background-position: -66px -108px !important;
}
.pop-info{
    width: 16px;
    height: 16px;
    display: inline-block;
    white-space: nowrap;
    background:url(/static/img/iconset_aside.19c56ea.png) no-repeat;
    background-size: 150px 150px;
    color: transparent;
    position: absolute;
    top: -3px;
    left: 5px;
}
.backdrop{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    background-color:rgba(0,0,0,.0);
}
.popup-layer{
    position: fixed;
    /* top: 136px; */
    left: 0px;
    right: 0px;
    z-index: 9999;
    padding: 12px 40px 12px 16px;
    border: 1px solid #abb4c4;
    margin:0 16px;
    /* -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.1); */
    /* box-shadow: 0 3px 6px rgba(0,0,0,0.1); */
    background-color: #fff;
    font-size: 16px;
    color: #424242;
    line-height: 23px;
    margin-top: 0px;
}
.popup-layer .title{
	font-size: 14px;
	font-weight: 500;
	line-height: 20px;
	margin-top: 14px;
	margin-bottom: 6px;
	display: block;
}
.popup-layer .title:first-child{
	margin-top: 0;
}
.popup-layer p{
	color: #999 !important;
	font-size: 13px !important;
	line-height: 18px;
	margin: 0;
}
.popup-layer p::before{
	content: "*";
	margin-right: 5px;
}
.popup-layer .btn-close,
.alert-desc .btn-close{
    position: absolute;
    top: 0;
    right: 0;
    width: 48px;
    height: 48px;
    border: 0;
    background: none;
    font-size: 0;
}
.popup-layer .btn-close .icon-close,
.alert-desc .btn-close .icon-close{
    overflow: hidden;
    display: inline-block;
    vertical-align: top;
    background: url(/static/img/iconset_aside.19c56ea.png) no-repeat;
    background-size: 150px 150px;
    color: transparent;
    background-position: -90px -108px;
    width: 18px;
    height: 18px;
}
